@use '../additional/mixins.scss' as *;
.advantages {
  &__title {
  }
  &__subtitle {
    margin-bottom: 2.5rem;
    max-width: 55rem;
    font-size: var(--fz-24);
    color: var(--white);
    opacity: 0.8;
  }

  &__text {
    display: block;
    font-family: var(--ff-2);
    font-size: var(--fz-32);
    font-weight: var(--fw-500);
    font-style: italic;
    color: var(--clr-orange);
    margin-bottom: 5rem;
    opacity: 0.8;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: -25px;
      left: 30px;
      background-color: var(--clr-orange);
      border-radius: 16px;
      width: 19.5rem;
      height: 106%;
    }
  }
  &__item {
    @include default;
    backdrop-filter: blur(45px);
    display: flex;
    align-items: center;
    gap: 3.5rem;
    padding: 2.8rem;
    text-transform: uppercase;
    font-size: var(--fz-32);
    font-weight: var(--fw-700);
    line-height: 1.3em;

    svg {
      height: 78px;
      max-width: 120px;
      width: 100%;
      margin-left: 30px;
    }
  }
}

@include breakpoint-down(medium) {
  .advantages {
    &__list {
      &::before {
        height: 105%;
        width: 16rem;
      }
    }
    &__item {
      flex-direction: column;
      align-items: flex-start;

      svg {
        margin: 0;
      }
    }
  }
}

@include breakpoint-down(small) {
  .advantages {
    padding-bottom: 5rem;
    &__list {
      &::before {
        width: 14rem;
        height: 104%;
      }
    }
    &__item {
      flex-direction: column;
      font-size: 1.6rem;
    }
  }
}

@include breakpoint-down(xsmall) {
  .advantages {
    &__list {
      &::before {
        width: 12rem;
        height: 104%;
      }
    }
  }
}
