:root {
  --main-fz: 62.5%;
  --ff-1: "Gilroy", sans-serif;
  --ff-2: "Magistral", sans-serif;
  --white: #fff;
  --container: 1210px;
  --clr-orange: #F0AF0A;
  --clr-black: #051022;
  --clr-green: #61B67F;
  --fz-8: 0.8rem;
  --fz-12: 1.2rem;
  --fz-14: 1.4rem;
  --fz-16: 1.6rem;
  --fz-18: 1.8rem;
  --fz-20: 2rem;
  --fz-24: 2.4rem;
  --fz-26: 2.6rem;
  --fz-28: 2.8rem;
  --fz-32: 3.2rem;
  --fz-40: 4rem;
  --fz-48: 4.8rem;
  --fz-56: 5.6rem;
  --fw-100: 100;
  --fw-200: 200;
  --fw-300: 300;
  --fw-400: 400;
  --fw-500: 500;
  --fw-600: 600;
  --fw-700: 700;
  --fw-800: 800;
  --fw-900: 900;
  --i-lower: 10;
  --i-low: 20;
  --i-mid: 30;
  --i-high: 40;
  --i-top: 100;
}

html {
  font-size: var(--main-fz);
}

body {
  font-family: var(--ff-1);
  font-size: 1.6rem;
  font-weight: var(--fw-400);
  line-height: 1.2em;
  letter-spacing: 0.2px;
  color: var(--white);
  background-color: var(--clr-black);
  min-height: 100%;
  display: grid;
  grid-template-rows: 1fr min-content;
  align-content: start;
  position: relative;
}

body::before {
  content: "";
  position: absolute;
  inset: 0;
  z-index: -1;
  opacity: 0.8;
  background-image: url("../img/bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
}

.container {
  max-width: var(--container);
  width: min(100% - 30px, 1210px);
  margin-inline: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.2em;
  font-weight: var(--fw-700);
  text-transform: uppercase;
  letter-spacing: 0.2px;
}

h1 {
  font-family: var(--ff-2);
  font-size: var(--fz-48);
  color: var(--white);
}

h2 {
  font-size: var(--fz-48);
  font-style: italic;
  color: var(--white);
  margin-bottom: 6rem;
}

h3 {
  font-size: var(--fz-24);
}

p {
  line-height: 1.2em;
}

span {
  display: block;
  line-height: 1.2em;
}

section {
  padding: 6.5rem 0;
}

.btn {
  display: block;
  width: 100%;
  max-width: 38rem;
  font-family: var(--ff-1);
  font-size: var(--fz-28);
  font-weight: var(--fw-500);
  color: var(--white);
  text-align: center;
  background-color: var(--clr-orange);
  padding: 1.8rem 2rem;
  border-radius: 16px;
  cursor: pointer;
  transition: 0.1s linear;
}

.btn:hover {
  background-color: #E2A300;
  box-shadow: 0px 4px 50px -12px rgba(240, 176, 11, 0.5);
}

.btn:active {
  transform: scale(0.95);
}

.main {
  max-width: 144rem;
  margin-inline: auto;
}

.hero {
  padding: 0;
  position: relative;
}

.hero__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 88vh;
}

.hero__inner::after {
  content: "";
  position: absolute;
  top: 0;
  right: -110px;
  z-index: var(--i-lower);
  background: url("../img/hero-img.png") no-repeat center/cover;
  width: 60rem;
  height: 78rem;
}

.hero__inner::before {
  content: "";
  position: absolute;
  top: 0;
  left: 45%;
  background: url("../img/hero-icon.png") no-repeat center/contain;
  width: 40px;
  height: 202px;
}

.hero__title {
  max-width: 61rem;
  margin-bottom: 2rem;
}

.hero__title span {
  color: var(--clr-orange);
  font-style: italic;
}

.hero__subtitle {
  font-family: var(--ff-2);
  text-transform: uppercase;
  font-size: var(--fz-28);
  letter-spacing: 0.2px;
  max-width: 42rem;
  color: var(--white);
  margin-bottom: 5rem;
  position: relative;
  z-index: var(--i-low);
}

.hero__date {
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(4px);
  font-family: var(--ff-2);
  color: var(--white);
  font-size: var(--fz-28);
  font-weight: var(--fw-500);
  text-align: center;
  padding: 1.8rem 2rem;
  max-width: 38rem;
  border-radius: 8px;
  text-transform: uppercase;
  margin-bottom: 3rem;
  overflow: hidden;
}

.hero__date::before,
.hero__date::after {
  content: "";
  position: absolute;
  background-color: var(--clr-orange);
  width: 30px;
  height: 30px;
  transform: rotate(45deg);
}

.hero__date::before {
  bottom: -15px;
  left: -15px;
}

.hero__date::after {
  top: -15px;
  right: -15px;
}

.opportunities__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 2rem;
}

.opportunities__item {
  display: flex;
  flex-direction: column;
  padding: 3rem 2.5rem;
  max-width: 38rem;
  width: 100%;
  height: 390px;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.4);
  backdrop-filter: blur(15px);
  border-radius: 15px;
}

.opportunities__icon {
  width: 100%;
  margin-top: auto;
}

.opportunities__icon--ukr {
  position: relative;
  bottom: -32px;
  right: -25px;
}

.opportunities__heading {
  font-style: italic;
  margin-bottom: 1.5rem;
}

.opportunities__text {
  max-width: 29.5rem;
  margin-bottom: 3rem;
}

.opportunities__wrapp {
  display: flex;
  align-items: center;
  margin-top: 2.5rem;
}

.opportunities .svg-1 {
  width: 257px;
}

.opportunities .svg-2 {
  filter: drop-shadow(0px 0px 18px rgba(240, 176, 11, 0.8));
  width: 64px;
  height: 64px;
}

.opportunities__stats {
  font-size: var(--fz-12);
}

.opportunities__stats span {
  display: inline;
  color: var(--clr-green);
}

.opportunities__stats svg {
  width: 10px;
  height: 10px;
}

.program__title {
  margin-bottom: 2.5rem;
}

.program__subtitle {
  font-family: var(--ff-2);
  margin-bottom: 5rem;
  color: var(--clr-orange);
  font-size: var(--fz-32);
  font-weight: var(--fw-500);
}

.program__list {
  display: flex;
  flex-direction: column;
  gap: 4rem;
  position: relative;
}

.program__list::before,
.program__list::after {
  content: "";
  position: absolute;
  bottom: -70px;
  left: 0;
  height: 24px;
}

.program__list::before {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(4px);
  border-radius: 38px;
  width: 100%;
}

.program__list::after {
  background-color: var(--clr-orange);
  box-shadow: 0px 0px 15px rgba(240, 176, 11, 0.6), 0px 0px 10px rgba(240, 176, 11, 0.5);
  border-radius: 50px;
  width: 25%;
}

.program__item {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
}

.program__item:not(:last-child) {
  border-bottom: 2px dashed rgba(255, 255, 255, 0.2);
  padding-bottom: 4rem;
}

.program__icon {
  align-self: flex-start;
}

.program__heading {
  font-style: italic;
  text-transform: uppercase;
}

.program__block {
  max-width: 58rem;
  width: 100%;
}

.program__block:first-child {
  min-width: 100%;
  max-width: 100%;
  padding: 2.5rem 5rem 2.5rem 6rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 3rem;
  flex-wrap: wrap;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.4);
  backdrop-filter: blur(15px);
  border-radius: 15px;
}

.program__block:first-child .program__heading {
  color: var(--clr-orange);
  text-shadow: 0px 0px 15px rgba(240, 176, 11, 0.4);
  margin-bottom: 1.5rem;
}

.program__block .program__icon {
  max-width: 30rem;
  width: 100%;
  height: 270px;
}

.program__block:nth-child(2) {
  background-color: var(--clr-orange);
  border-radius: 15px;
  padding: 3rem;
}

.program__block:nth-child(2) .program__heading {
  margin-bottom: 1.5rem;
}

.program__block:nth-child(2) .program__text {
  font-size: var(--fz-18);
  font-weight: var(--fw-500);
  line-height: 1.3em;
}

.program__block:last-child {
  border: 1px solid var(--clr-orange);
  border-radius: 15px;
  padding: 3rem 2rem;
}

.program__block .program__heading {
  margin-bottom: 2.5rem;
}

.program__block .program__bonuses {
  display: grid;
  grid-template-columns: 28rem 23.5rem;
  gap: 1.5rem 2rem;
}

.program__block .program__bonus {
  counter-increment: my-counter;
  width: fit-content;
  font-weight: var(--fw-600);
  font-style: italic;
  line-height: 1.3em;
  align-self: center;
  margin-left: 4rem;
  position: relative;
}

.program__block .program__bonus::before {
  content: counter(my-counter);
  position: absolute;
  top: 0;
  left: -4rem;
  bottom: 0;
  text-shadow: 0px 0px 10px var(--clr-orange);
  color: var(--clr-orange);
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.4);
  backdrop-filter: blur(15px);
  border-radius: 15px;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-block: auto;
  padding: 6px;
  width: 30px;
  height: 30px;
}

.program__descr {
  text-transform: uppercase;
  font-size: var(--fz-26);
  line-height: 1.35em;
  font-weight: var(--fw-700);
  max-width: 67rem;
  margin-bottom: 1.6rem;
}

.program__wrapp {
  max-width: 73.5rem;
  width: 100%;
}

.program-plan {
  padding: 1.8rem 2.5rem;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.4);
  width: 100%;
  position: relative;
}

.program-plan__wrapp {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}

.program-plan .program-plan-cross {
  width: 27px;
  height: 27px;
  opacity: 0.4;
  transition: 0.1s linear;
}

.program-plan.active .program-plan-cross {
  transform: rotate(45deg);
}

.program-plan.active .program-plan__list {
  display: grid;
}

.program-plan.active span {
  margin-bottom: 1.5rem;
}

.program-plan span {
  font-style: italic;
  text-transform: uppercase;
  font-weight: var(--fw-500);
  font-size: var(--fz-18);
  line-height: 1.3em;
  color: var(--white);
  opacity: 0.4;
  cursor: pointer;
}

.program-plan__list {
  column-count: 2;
}

.program-plan__item {
  max-width: 36rem;
  font-weight: var(--fw-500);
  font-size: var(--fz-18);
  line-height: 1.3em;
  margin-bottom: 1rem;
  padding-left: 1.3rem;
  position: relative;
}

.program-plan__item::before {
  content: "";
  position: absolute;
  top: 0.5em;
  left: 0;
  display: inline-block;
  width: 8px;
  height: 8px;
  background-color: var(--clr-orange);
  box-shadow: 0px 0px 8px rgba(240, 176, 11, 0.8);
  margin-right: 8px;
  border-radius: 100vmax;
}

.program__btn {
  margin-inline: auto;
  background-color: var(--clr-orange);
  text-transform: none;
  font-size: var(--fz-32);
  font-weight: var(--fw-600);
  max-width: 44.5rem;
  padding: 1.7rem 2rem;
}

.ticket__inner {
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(15px);
  border-radius: 16px;
  padding: 2rem 8rem 4rem;
}

.ticket__block {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 4.5rem;
  padding-bottom: 4.5rem;
  border-bottom: 2px dashed rgba(255, 255, 255, 0.4);
}

.ticket__img {
  width: 95px;
  height: 95px;
  display: inline;
}

.ticket__title {
  text-transform: uppercase;
  font-size: var(--fz-40);
  margin: 0;
}

.ticket__wrapp {
  display: flex;
  align-items: center;
}

.ticket__text {
  margin-bottom: 8px;
  opacity: 0.4;
  text-align: right;
}

.ticket__price {
  display: block;
  font-size: var(--fz-48);
  font-weight: var(--fw-500);
}

.ticket__price span {
  display: inline-block;
  color: var(--clr-orange);
}

.ticket__btn {
  max-width: 100%;
}

.ticket__wrapper {
  margin-bottom: 2rem;
}

.ticket__wrapper span,
.ticket__wrapper i {
  display: inline-block;
  margin: 0;
}

.ticket__wrapper .ticket__info {
  display: none;
}

.ticket__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1.5rem;
}

.ticket__info--temporarily {
  display: block;
}

.ticket-elm {
  display: none;
  max-width: 43rem;
  width: 100%;
  height: 25rem;
  transform: translateX(-70px);
}

.result__list {
  display: flex;
  flex-wrap: wrap;
  gap: 4rem;
  margin-bottom: 9rem;
}

.result__item {
  display: flex;
  align-items: center;
  gap: 3rem;
  padding: 2.5rem 3.5rem;
  width: 100%;
  max-width: 56rem;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.4);
  backdrop-filter: blur(15px);
  border-radius: 15px;
  border-color: var(--clr-orange);
  border-radius: 8px;
}

.result__item:nth-child(even) {
  transform: translateY(50px);
}

.result__item:nth-child(2),
.result__item:nth-child(3),
.result__item:nth-child(6) {
  border-color: rgba(255, 255, 255, 0.4);
}

.result__img {
  max-width: 14.2rem;
  object-fit: cover;
}

.result__num {
  font-size: var(--fz-48);
  font-weight: var(--fw-800);
}

.result__heading {
  max-width: 66.5rem;
  margin-bottom: 1.3rem;
  font-size: var(--fz-28);
  font-style: italic;
}

.result__heading + .result__text {
  font-size: var(--fz-18);
}

.result__text {
  line-height: 1.3em;
  font-size: var(--fz-28);
  font-weight: var(--fw-700);
}

.result i {
  color: var(--clr-orange);
}

.result__btn {
  padding: 1.7rem 2rem;
  margin-inline: auto;
  background-color: var(--clr-orange);
}

.result-elm {
  display: none;
  width: 100%;
  height: 307px;
  margin-inline: auto;
  margin-top: 2rem;
}

