  .ticket-elm {
    display: block;
  }

  .result__list {
    gap: 2rem;
  }

  .result__item {
    padding: 2rem;
    gap: 1.5rem;
  }

  .result__heading,
  .result__text {
    font-size: 1.8rem;
  }

  .result__heading + .result__text,
  .result__text + .result__text {
    font-size: 1.4rem;
  }

  .result-elm {
    display: block;
  }

  .advantages {
    padding-bottom: 5rem;
  }

  .advantages__list::before {
    width: 14rem;
    height: 104%;
  }

  .advantages__item {
    flex-direction: column;
    font-size: 1.6rem;
  }

  .author__list {
    gap: 2rem 1rem;
  }

  .author__img {
    top: 60px;
    left: 0;
    max-width: 36rem;
  }

  .author__item {
    max-width: 16.5rem;
  }

  .faq__questions {
    font-size: 1.4rem;
  }

  .faq__item {
    padding-right: 3rem;
  }

  .faq__icons-wrapp:first-of-type {
    top: 100px;
  }

  .faq__icons-wrapp:last-of-type {
    top: 145px;
  }
}

@media (max-width: 375.98px) {
  :root {
    --fz-8: 0.8rem;
    --fz-12: 1.2rem;
    --fz-14: 1.4rem;
    --fz-16: 1.6rem;
    --fz-18: 1.8rem;
    --fz-20: 2rem;
    --fz-24: 2rem;
    --fz-26: 2rem;
    --fz-28: 2.2rem;
    --fz-32: 2.4rem;
    --fz-40: 2.6rem;
    --fz-48: 2.8rem;
    --fz-56: 3rem;
  }

  .btn {
    max-width: 100%;
  }

  .hero__date {
    max-width: 100%;
  }

  .hero__inner {
    text-align: left;
  }

  .hero__inner::after {
    max-width: 31rem;
  }

  .btn {
    max-width: 100%;
  }

  .bonus__name {
    font-size: 1.4rem;
  }

  .bonus__text {
    font-size: 1.2rem;
  }

  .advantages__list::before {
    width: 12rem;
    height: 104%;
  }

  .faq__item {
    padding: 2rem;
  }
}