@use 'additional/mixins' as *;

html {
  font-size: var(--main-fz);
}

body {
  font-family: var(--ff-1);
  font-size: 1.6rem;
  font-weight: var(--fw-400);
  line-height: 1.2em;
  letter-spacing: 0.2px;
  color: var(--white);
  background-color: var(--clr-black);
  min-height: 100%;
  display: grid;
  grid-template-rows: 1fr min-content;
  align-content: start;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    inset: 0;
    z-index: -1;
    opacity: 0.8;
    background-image: url('../img/bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
  }
}

.container {
  max-width: var(--container);
  width: min(calc(100% - 30px), 1210px);
  margin-inline: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.2em;
  font-weight: var(--fw-700);
  text-transform: uppercase;
  letter-spacing: 0.2px;
}

h1 {
  font-family: var(--ff-2);
  font-size: var(--fz-48);
  color: var(--white);
}

h2 {
  font-size: var(--fz-48);
  font-style: italic;
  color: var(--white);
  margin-bottom: 6rem;
}

h3 {
  font-size: var(--fz-24);
}

p {
  line-height: 1.2em;
}

span {
  display: block;
  line-height: 1.2em;
}

section {
  padding: 6.5rem 0;
}

@include breakpoint-down(extra-big) {
  section {
    padding: 5rem;
  }
  h2 {
    text-align: left;
  }
}

@include breakpoint-down(big) {
  section {
    padding: 4rem 0;
  }

  h2 {
    margin-bottom: 4rem;
  }
}

@include breakpoint-down(medium) {
  section {
    padding: 3.5rem 0;
  }
  h2 {
    margin-bottom: 3rem;
  }
}

@include breakpoint-down(regular) {
  section {
    padding: 3rem 0;
  }
  h2 {
    margin-bottom: 2.5rem;
  }
}

@include breakpoint-down(small) {
  section {
    padding: 2.5rem 0;
  }
}
