@use '../additional/mixins' as *;
.modal-content {
  background-color: #030e22;
  border-radius: 20px;
  max-width: 80rem;
  width: 100%;
  padding: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: relative;
  margin: auto 2rem;

  z-index: 2;

  &__icon {
    margin-bottom: 3rem;
    width: 121px;
    height: 121px;
  }
  &__title {
    font-family: var(--ff-2);
    font-weight: var(--fw-500);
    margin-bottom: 1rem;
    font-style: normal;
    text-align: center;
  }
  &__text {
    text-align: center;
    margin-bottom: 2rem;
    font-size: var(--fz-18);

    strong {
      display: block;
      margin-top: 1rem;
    }
  }
  &__btn {
    padding: 1rem 1.5rem;
    font-size: var(--fz-18);
    margin-bottom: 2.5rem;
    max-width: 27.5rem;
  }
  &__wrapp {
    display: flex;
    gap: 2rem;
  }
  &__link {
    background: rgba(255, 255, 255, 0.1);
    border: 0.5px solid #9e9e9e;
    backdrop-filter: blur(5px);
    border-radius: 8px;
    padding: 8px;
    width: 40px;
    height: 40px;
  }
  &__social {
    fill: var(--clr-orange);
    filter: drop-shadow(0px 0px 18px rgba(240, 176, 11, 0.8));
    width: 24px;
    height: 24px;
  }
}

.close {
  position: absolute;
  right: -150px;
  top: -50px;
  svg {
    width: 60px;
    height: 60px;
  }
}

.modal {
  position: fixed;
  width: 100%;
  min-height: 100vh;
  z-index: 999;
  background: url('../img/modal-bg.png') no-repeat center / cover;
  display: flex;
  justify-content: center;
  align-items: center;
  
  &.active {
    display: flex;
  }
}

.overflow {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: rgba(#030e22, 0.4);
  backdrop-filter: blur(15px);
}

@include breakpoint-down(extra-big) {
  .close {
    right: -100px;
  }
}

@include breakpoint-down(big) {
  .close {
    top: 20px;
    right: 20px;
  }
}
