@use 'additional/mixins' as *;

.ticket {
  &__inner {
    background-color: rgba(#ffffff, 0.1);
    backdrop-filter: blur(15px);
    border-radius: 16px;
    padding: 2rem 8rem 4rem;
  }

  &__block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 4.5rem;
    padding-bottom: 4.5rem;
    border-bottom: 2px dashed rgba(#fff, 0.4);
  }

  &__img {
    width: 95px;
    height: 95px;
    display: inline;
  }
  &__title {
    text-transform: uppercase;
    font-size: var(--fz-40);
    margin: 0;
  }
  &__wrapp {
    display: flex;
    align-items: center;
  }
  &__text {
    margin-bottom: 8px;
    opacity: 0.4;
    text-align: right;
  }
  &__price {
    display: block;
    font-size: var(--fz-48);
    font-weight: var(--fw-500);

    span {
      display: inline-block;
      color: var(--clr-orange);
    }
  }
  &__btn {
    max-width: 100%;
  }

  &__wrapper {
    margin-bottom: 2rem;
    span,
    i {
      display: inline-block;
      margin: 0;
    }

    .ticket__info {
      display: none ;
    }
  }

  &__info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1.5rem;

    &--temporarily {
      display: block;
    }
  }

  &-elm {
    display: none;
    max-width: 43rem;
    width: 100%;
    height: 25rem;
    transform: translateX(-70px);

  }
}


@include breakpoint-down(extra-big) {
  .ticket {
    &__inner {
      padding-inline: 5rem;
    }
  }
}

@include breakpoint-down(big) {
  .ticket {
    &__inner {
      padding-inline: 4rem;
    }
    &__block {
      margin-bottom: 3rem;
      padding-bottom: 3rem;
    }

    &__wrapper {
      display: flex;
      .ticket__info {
        display: flex;
        width: 100%;
      }
    }

    &__info {
      &--temporarily {
        display: none;
      }
    }
  }
}

@include breakpoint-down(medium) {
  .ticket {
    padding-top: 7.5rem;
    &__inner {
      padding-inline: 3rem;
    }
  }
}

@include breakpoint-down(regular) {
  .ticket {
    &__block {
      margin-bottom: 2rem;
      padding-bottom: 2rem;
    }
    &__img {
      position: absolute;
      top: -60px;
      right: 15px;
      transform: rotate(-50deg);
    }
  }
}
@include breakpoint-down(small) {
  .ticket-elm {
    display: block;
  }
}
