@use '../additional/mixins' as *;

.footer {
  border-top: 1px solid #9e9e9e;
  padding: 5rem 0 8rem;
  color: var(--white);

  &__inner {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: flex-end;
    gap: 3rem;
  }
  &__wrapp {
    display: flex;
    flex-direction: column;
    font-weight: var(--fw-500);
    &:last-child {
      align-items: flex-end;
    }
  }
  &__name {
    margin-bottom: 4rem;
    font-size: var(--fz-40);
    font-weight: var(--fw-800);
    color: var(--clr-orange);
  }
  &__info {
    opacity: 0.5;
    margin-bottom: 0.8rem;
  }
  &__info-link {
    opacity: 0.5;
    transition: 0.1s linear;

    &:first-of-type {
      margin-right: 2.5rem;
    }

    &:hover {
      text-decoration: underline;
      opacity: 1;
    }
  }
  &__list {
    margin-bottom: 3rem;
    display: flex;
    align-items: center;
    gap: 3rem;
  }

  &__link {
    svg {
      width: 24px;
      height: 24px;
      fill: #dfdfdf;
    }
    &:hover {
      svg {
        fill: var(--clr-orange);
      }
    }
  }
  &__phone {
    font-weight: var(--fw-500);
    margin-bottom: 0.8rem;
  }
}

.link {
  transition: 0.1s linear;
  opacity: 0.5;

  &:hover {
    color: var(--clr-orange);
    opacity: 1;
  }
}

.media-show {
  display: none;
}

@include breakpoint-down(medium) {
  .footer {
    &__inner {
      gap: 2rem;
      justify-content: center;

      .media-show {
        display: flex !important;
        text-align: center;
        order: -1;
        margin-bottom: 1.5rem;
      }
    }
    &__wrapp {
      justify-content: center;
      text-align: center;
      width: 100%;
      &:last-child {
        order: -1;
        align-items: center;
      }
    }

    &__name {
      display: none;
    }

    &__info-link {
      display: block;

      &:first-of-type {
        margin: 0 0 1rem 0;
      }
    }
  }
}

