@use '../additional/mixins' as *;
.hero {
  padding: 0;
  position: relative;

  &__inner {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 88vh;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: -110px;
      z-index: var(--i-lower);
      background: url('../img/hero-img.png') no-repeat center / cover;
      width: 60rem;
      height: 78rem;
    }
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 45%;
      background: url('../img/hero-icon.png') no-repeat center / contain;
      width: 40px;
      height: 202px;
    }
  }
  &__title {
    max-width: 61rem;
    margin-bottom: 2rem;

    span {
      color: var(--clr-orange);
      font-style: italic;
    }
  }
  &__subtitle {
    font-family: var(--ff-2);
    text-transform: uppercase;
    font-size: var(--fz-28);
    letter-spacing: 0.2px;
    max-width: 42rem;
    color: var(--white);
    margin-bottom: 5rem;
    position: relative;
    z-index: var(--i-low);
  }

  &__date {
    background-color: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(4px);
    font-family: var(--ff-2);
    color: var(--white);
    font-size: var(--fz-28);
    font-weight: var(--fw-500);
    text-align: center;
    padding: 1.8rem 2rem;
    max-width: 38rem;
    border-radius: 8px;
    text-transform: uppercase;
    margin-bottom: 3rem;
    overflow: hidden;

    &::before,
    &::after {
      content: '';
      position: absolute;
      background-color: var(--clr-orange);
      width: 30px;
      height: 30px;
      transform: rotate(45deg);
    }

    &::before {
      bottom: -15px;
      left: -15px;
    }

    &::after {
      top: -15px;
      right: -15px;
    }
  }
}
@include breakpoint-down(large) {
  .hero {
    &__inner {
      &::after {
        right: 0;
      }
    }
  }
}
@include breakpoint-down(extra-big) {
  .hero {
    padding-top: 3rem;
    padding-bottom: 3.5rem;

    &__inner {
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;

      &::after {
        position: static;
        max-width: 67rem;
        width: 100%;
        max-height: 67rem;
        margin-bottom: 3rem;
      }

      &::before {
        top: -30px;
        right: 0;
        left: unset;
      }
    }
    &__subtitle {
      margin-bottom: 1rem;
    }

    &__date {
      order: 1;
      max-width: 38rem;
      width: 100%;
      text-align: center;
    }
    &__btn {
      order: 2;
    }
  }
}

@include breakpoint-down(big) {
  .hero {
    &__inner::after {
      max-height: 65rem;
    }
  }
}

@include breakpoint-down(medium) {
  .hero {
    &__inner::after {
      max-height: 60rem;
      max-width: 50rem;
    }
  }
}

@include breakpoint-down(regular) {
  .hero {
    &__inner::after {
      max-height: 50rem;
      max-width: 45rem;
    }
    &__date {
      margin-bottom: 1.5rem;
    }
  }
}

@include breakpoint-down(small) {
  .hero__inner {
    text-align: left;

    &::after {
      max-height: 40rem;
    }
  }
}

@include breakpoint-down(xsmall) {
  .hero__date {
    max-width: 100%;
  }
  .hero__inner {
    text-align: left;

    &::after {
      max-width: 31rem;
    }
  }
}
