@use '../additional/mixins' as *;

.btn {
  display: block;
  width: 100%;
  max-width: 38rem;
  font-family: var(--ff-1);
  font-size: var(--fz-28);
  font-weight: var(--fw-500);
  color: var(--white);
  text-align: center;
  background-color: var(--clr-orange);
  padding: 1.8rem 2rem;
  border-radius: 16px;
  cursor: pointer;
  transition: 0.1s linear;

  &:hover {
    background-color: #E2A300;
    box-shadow: 0px 4px 50px -12px rgba(240, 176, 11, 0.5);
  }

  &:active {
    transform: scale(0.95);
  }
}

@include breakpoint-down(small) {
  .btn {
    border-radius: 6px;

  }
}

@include breakpoint-down(xsmall) {
  .btn {
    max-width: 100%;
  }
}
