@use '../additional/mixins.scss' as *;
.result {
  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 4rem;
    margin-bottom: 9rem;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 3rem;
    padding: 2.5rem 3.5rem;
    width: 100%;
    max-width: 56rem;
    @include default;
    border-color: var(--clr-orange);
    border-radius: 8px;

    &:nth-child(even) {
      transform: translateY(50px);
    }

    &:nth-child(2),
    &:nth-child(3),
    &:nth-child(6) {
      border-color: rgba(255, 255, 255, 0.4);
    }
  }
  &__img {
    max-width: 14.2rem;
    object-fit: cover;
  }

  &__num {
    font-size: var(--fz-48);
    font-weight: var(--fw-800);
  }

  &__heading {
    max-width: 66.5rem;
    margin-bottom: 1.3rem;
    font-size: var(--fz-28);
    font-style: italic;
    & + .result__text {
      font-size: var(--fz-18);
    }
  }
  &__text {
    line-height: 1.3em;
    font-size: var(--fz-28);
    font-weight: var(--fw-700);
  }

  i {
    color: var(--clr-orange);
  }
  &__btn {
    padding: 1.7rem 2rem;
    margin-inline: auto;
    background-color: var(--clr-orange);
  }

  &-elm {
    display: none;
    width: 100%;
    height: 307px;
    margin-inline: auto;
    margin-top: 2rem;
  }
}

@include breakpoint-down(extra-big) {
  .result {
    &__list {
      justify-content: center;
    }

    &__item {
      max-width: 80rem;
      &:nth-child(even) {
        transform: translate(0);
        border-color: rgba(255, 255, 255, 0.4) !important;
      }

      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(6) {
        border-color: var(--clr-orange);
      }
    }
  }
}

@include breakpoint-down(big) {
  .result {
    &__list {
      margin-bottom: 5rem;
    }
  }
}

@include breakpoint-down(medium) {
}

@include breakpoint-down(regular) {
  .result {
    &__list {
      margin-bottom: 4rem;
      gap: 3rem;
    }
  }
}

@include breakpoint-down(small) {
  .result {
    &__list {
      gap: 2rem;
    }
    &__item {
      padding: 2rem;
      gap: 1.5rem;
    }

    &__heading,
    &__text {
      font-size: 1.8rem;
      & + .result__text {
        font-size: 1.4rem;
      }
    }

    &-elm {
      display: block;
    }
  }
}

@include breakpoint-down(xsmall) {
  .result {
  }
}
