@use '../additional/mixins.scss' as *;
.bonus {
  &__list {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    gap: 2rem;
    margin-bottom: 12rem;
  }
  &__item {
    display: flex;
    align-items: center;
    flex-direction: column;
    max-width: 28rem;
    height: 330px;
    min-height: 160px;
    width: 100%;
    padding: 4rem 2.7rem 3rem;
    @include default;

    &:nth-child(even) {
      transform: translateY(45px);
    }

    svg {
      width: 56px;
      height: 56px;
      filter: drop-shadow(0px 0px 18px rgba(240, 176, 11, 0.8));
    }
  }

  &__icon {
    max-width: 120px;
    max-height: 120px;
    width: 100%;
    height: 100%;
    border-radius: 100vmax;
    background-color: rgba(255, 255, 255, 0.1);
    border: 0.5px solid rgba(255, 255, 255, 0.2);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__name {
    text-align: center;
    margin-bottom: 1.5rem;
    margin-top: auto;
  }
  &__text {
    text-align: center;
  }

  &__btn {
    background-color: var(--clr-orange);
    font-size: var(--fz-32);
    font-weight: var(--fw-600);
    max-width: 44.5rem;
    padding: 1.7rem 2rem;
    margin-inline: auto;
  }
}

@include breakpoint-down(extra-big) {
  .bonus {
    &__list {
      margin-bottom: 6rem;
    }

    &__item {
      &:nth-child(even) {
        transform: translate(0);
      }
    }
  }
}

@include breakpoint-down(big) {
  .bonus__list {
    margin-bottom: 4rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
  }
}

@include breakpoint-down(medium) {
}

@include breakpoint-down(regular) {
  .bonus {
    &__list {
      gap: 1rem;
      margin-bottom: 5rem;
    }
    &__item {
      height: unset;
      padding: 1rem 1.5rem;
      &:nth-child(even) {
        transform: translateY(30px);
      }
    }
    &__icon {
      margin-bottom: 1rem;
      width: 56px;
      height: 56px;

      svg {
        width: 30px;
        height: 30px;
      }
    }

    &__name {
      font-size: 1.6rem;
    }

    &__text {
      font-size: 1.4rem;
    }
  }
}

@include breakpoint-down(xsmall) {
  .bonus {
    &__name {
      font-size: 1.4rem;
    }

    &__text {
      font-size: 1.2rem;
    }
  }
}
