@use '../additional/mixins.scss' as *;
.opportunities {

  &__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 2rem;
  }
  &__item {
    display: flex;
    flex-direction: column;
    padding: 3rem 2.5rem;
    max-width: 38rem;
    width: 100%;
    height: 390px;
    overflow: hidden;
    @include default;
  }

  &__icon {
    width: 100%;
    margin-top: auto;

    &--ukr {
      position: relative;
      bottom: -32px;
      right: -25px;
    }
  }
  &__heading {
    font-style: italic;
    margin-bottom: 1.5rem;
  }
  &__text {
    max-width: 29.5rem;
    margin-bottom: 3rem;
  }

  &__wrapp {
    display: flex;
    align-items: center;
    margin-top: 2.5rem;
  }

  .svg-1 {
    width: 257px;
  }

  .svg-2 {
    filter: drop-shadow(0px 0px 18px rgba(240, 176, 11, 0.8));
    width: 64px;
    height: 64px;
  }
  &__stats {
    font-size: var(--fz-12);
    span {
      display: inline;
      color: var(--clr-green);
    }

    svg {
      width: 10px;
      height: 10px;
    }
  }
}

@include breakpoint-down(extra-big) {
  .opportunities {
    &__list {
      justify-content: center;
    }
  }
}

@include breakpoint-down(small) {
  .opportunities {
    &__item {
      height: unset;
      max-width: 100%;
    }
  }
}

@include breakpoint-down(xsmall) {
  .btn {
    max-width: 100%;
  }
}
