@use '../additional/mixins.scss' as *;
.faq {
  position: relative;

  &__content {
    display: flex;
    flex-direction: column;
  }
  &__title {
    text-align: center;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8.5rem;
    margin-bottom: 5rem;
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    width: 100%;
  }
  &__item {
    display: flex;
    flex-direction: column;
    padding: 3rem 3rem;
    max-width: 68rem;
    width: 100%;
    @include default;
    font-weight: var(--fw-600);
    position: relative;
  }

  &__wrapp {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;
    cursor: pointer;

    &._active {
      .faq__cross {
        transform: rotate(45deg);
      }
    }
  }

  &__cross {
    min-width: 30px;
    width: 30px;
    min-height: 30px;
    height: 30px;
    transition: 0.1s linear;
  }
  &__questions {
    text-transform: uppercase;
    font-size: var(--fz-20);
    font-weight: var(--fw-600);
    transition: 0.3s linear;
  }
  &__answer {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    margin-top: 1rem;

    p {
      a {
        display: inline;
        color: var(--clr-orange);
        &:hover {
          text-decoration: underline;
        }
      }
    }
  }

  &__icons {
    max-width: 47rem;
    height: 47rem;
    width: 100%;
    position: relative;

    &--temporarily {
      display: none;
    }

    &-wrapp {
      @include default;
      box-shadow: 12px 15px 50px rgba(240, 176, 11, 0.4);
      width: 110px;
      height: 110px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;

      &:first-of-type {
        top: 135px;
        left: 25px;
        z-index: 3;
      }

      &:last-of-type {
        left: 110px;
        top: 180px;
        z-index: 2;
      }
    }
  }

  &__icon-1 {
    max-width: 450px;
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 1;
  }

  &__icon-3 {

  }

  &__icon-2,
  &__icon-3 {
    width: 64px;
    height: 64px;
  }

  &__icon-2 {
    filter: drop-shadow(0px 0px 18px rgba(240, 176, 11, 0.8));
  }

  &__btn {
    font-size: var(--fz-32);
    font-weight: var(--fw-600);
    margin-inline: auto;
    max-width: 44.5rem;
    padding: 1.7rem 2rem;
    background-color: var(--clr-orange);
  }
}

@include breakpoint-down(extra-big) {
  .faq {
    &__icons {
      position: absolute;
      right: 0;
      z-index: -1;
    }
  }
}

@include breakpoint-down(medium) {
  .faq {
    &__inner {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      gap: 4rem;
    }
    &__icons {
      position: relative;
      right: 0;
      z-index: -1;
      order: -1;
      height: 40rem;
      display: none;

      &--temporarily {
        display: flex;
        margin-bottom: 2rem;
        margin-inline: auto;
      }
    }

    &__questions {
      span {
        display: inline;
      }
    }
  }
}

@include breakpoint-down(regular) {
  .faq {
    &__item {
      padding-right: 4rem;
    }

    &__icons {
      height: 30rem;
    }
  }
}

@include breakpoint-down(small) {
  .faq {
    &__questions {
      font-size: 1.4rem;
    }
    &__item {
      padding-right: 3rem;
    }

    &__icons {
      &-wrapp {

        &:first-of-type {
          top: 100px;
        }
        &:last-of-type {
          top: 145px;
        }
      }
    }

  }
}

@include breakpoint-down(xsmall) {
  .faq {
    &__item {
      padding: 2rem;
    }

  }
}
