@use '../additional/mixins.scss' as *;
.program {
  &__title {
    margin-bottom: 2.5rem;
  }

  &__subtitle {
    font-family: var(--ff-2);
    margin-bottom: 5rem;
    color: var(--clr-orange);
    font-size: var(--fz-32);
    font-weight: var(--fw-500);
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: 4rem;
    position: relative;

    &::before,
    &::after {
      content: '';
      position: absolute;
      bottom: -70px;
      left: 0;
      height: 24px;
    }

    &::before {
      background: rgba(255, 255, 255, 0.1);
      backdrop-filter: blur(4px);
      border-radius: 38px;
      width: 100%;
    }

    &::after {
      background-color: var(--clr-orange);
      box-shadow: 0px 0px 15px rgba(240, 176, 11, 0.6), 0px 0px 10px rgba(240, 176, 11, 0.5);
      border-radius: 50px;
      width: 25%;
    }
  }
  &__item {
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;

    &:not(:last-child) {
      border-bottom: 2px dashed rgba(#fff, 0.2);
      padding-bottom: 4rem;
    }
  }

  &__icon {
    align-self: flex-start;
  }

  &__heading {
    font-style: italic;
    text-transform: uppercase;
  }

  &__block {
    max-width: 58rem;
    width: 100%;

    &:first-child {
      min-width: 100%;
      max-width: 100%;
      padding: 2.5rem 5rem 2.5rem 6rem;
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 3rem;
      flex-wrap: wrap;
      @include default;

      .program__heading {
        color: var(--clr-orange);
        text-shadow: 0px 0px 15px rgba(240, 176, 11, 0.4);
        margin-bottom: 1.5rem;
      }
    }
    .program__icon {
      max-width: 30rem;
      width: 100%;
      height: 270px;
    }

    &:nth-child(2) {
      background-color: var(--clr-orange);
      border-radius: 15px;
      padding: 3rem;

      .program__heading {
        margin-bottom: 1.5rem;
      }

      .program__text {
        font-size: var(--fz-18);
        font-weight: var(--fw-500);
        line-height: 1.3em;
      }
    }

    &:last-child {
      border: 1px solid var(--clr-orange);
      border-radius: 15px;
      padding: 3rem 2rem;
    }
    .program__heading {
      margin-bottom: 2.5rem;
    }

    .program__bonuses {
      display: grid;
      grid-template-columns: 28rem 23.5rem;
      gap: 1.5rem 2rem;
    }

    .program__bonus {
      counter-increment: my-counter;
      width: fit-content;
      font-weight: var(--fw-600);
      font-style: italic;
      line-height: 1.3em;
      align-self: center;
      margin-left: 4rem;
      position: relative;

      &::before {
        content: counter(my-counter);
        position: absolute;
        top: 0;
        left: -4rem;
        bottom: 0;
        text-shadow: 0px 0px 10px var(--clr-orange);
        color: var(--clr-orange);
        @include default;
        border-radius: 4px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-block: auto;
        padding: 6px;
        width: 30px;
        height: 30px;
      }
    }
  }

  &__descr {
    text-transform: uppercase;
    font-size: var(--fz-26);
    line-height: 1.35em;
    font-weight: var(--fw-700);
    max-width: 67rem;
    margin-bottom: 1.6rem;
  }

  &__wrapp {
    max-width: 73.5rem;
    width: 100%;
  }
  &-plan {
    padding: 1.8rem 2.5rem;
    border-radius: 8px;
    border: 1px solid rgba(#fff, 0.4);
    width: 100%;
    position: relative;

    &__wrapp {
      display: flex;
      align-items: center;
      justify-content: space-between;
      cursor: pointer;
    }

    .program-plan-cross {
      width: 27px;
      height: 27px;
      opacity: 0.4;
      transition: 0.1s linear;
    }

    &.active {
      .program-plan-cross {
        transform: rotate(45deg);
      }
      .program-plan__list {
        display: grid;
      }
      span {
        margin-bottom: 1.5rem;
      }
    }
    span {
      font-style: italic;
      text-transform: uppercase;
      font-weight: var(--fw-500);
      font-size: var(--fz-18);
      line-height: 1.3em;
      color: var(--white);
      opacity: 0.4;
      cursor: pointer;
    }
    &__list {
      column-count: 2;
    }

    &__item {
      max-width: 36rem;
      font-weight: var(--fw-500);
      font-size: var(--fz-18);
      line-height: 1.3em;
      margin-bottom: 1rem;
      padding-left: 1.3rem;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        top: .5em;
        left: 0;
        display: inline-block;
        width: 8px;
        height: 8px;
        background-color: var(--clr-orange);
        box-shadow: 0px 0px 8px rgba(240, 176, 11, 0.8);
        margin-right: 8px;
        border-radius: 100vmax;
      }
    }
  }

  &__btn {
    margin-inline: auto;
    background-color: var(--clr-orange);
    text-transform: none;
    font-size: var(--fz-32);
    font-weight: var(--fw-600);
    max-width: 44.5rem;
    padding: 1.7rem 2rem;
  }
}

@include breakpoint-down(extra-big) {
  .program {
    &__item {
      justify-content: center;
    }
    &__block {
      max-width: 80rem;

      &:first-child {
        justify-content: center;
        min-width: unset;
        max-width: 80rem;
        padding: 3rem;
      }
    }
    &__wrapp {
      display: flex;
      flex-direction: column;
    }
    &__descr {
      max-width: 50rem;
    }
  }
}

@include breakpoint-down(big) {
  .program {
    &__list {
      margin-bottom: 3rem;
    }

    &__block {
      &:first-child {
        .program__icon {
          height: 200px;
        }
      }
    }
    &__descr {
      max-width: 100%;
    }
    &-plan {
      max-width: 100%;
    }
  }
}

@include breakpoint-down(medium) {
  .program {
    &-plan__list {
      column-count: 1;
    }

    &__block {
      .program__bonuses {
        grid-template-columns: 1fr;
      }
    }
  }
}

@include breakpoint-down(regular) {
  .program {
    &-plan {
      padding: 1.5rem;
    }

    &__block {
      &:first-child {
        justify-content: center;
        min-width: unset;
        max-width: 65rem;
      }
      &:first-child,
      &:nth-child(2),
      &:last-child {
        padding: 2rem;
      }
    }

    &__icon {
      display: none;
    }
  }
}

@include breakpoint-down(small) {
  .program {
    &__descr {
      font-size: 2rem;
    }
  }
}
