@use '../additional/mixins' as *;

.author {
  position: relative;

  &__title {
    margin-bottom: 7rem;
  }
  &__inner {
    display: flex;
    align-items: center;
  }
  &__list {

    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 5rem 7rem;
    margin-bottom: 10rem;
  }
  &__item {
    max-width: 29rem;
    width: 100%;
  }
  &__nums {
    font-size: var(--fz-56);
    font-weight: var(--fw-700);
    color: var(--clr-orange);
    padding-bottom: 1.5rem;
    margin-bottom: 1.5rem;
    max-width: 23rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  }
  &__descr {
    font-size: var(--fz-24);
    font-weight: var(--fw-700);
  }

  &__img {
    display: none;
    position: absolute;
    top: 0;
    z-index: -1;
    right: -140px;
    height: 790px;

    &--temporarily {
      display: block;
    }

    img {
      max-height: 815px;
      max-width: 680px;
      object-fit: cover;
    }
  }
}

@media (max-width: 1500px) {
  .author {
    &__img {
      max-width: 60rem;
      right: 0;

      img {
        object-fit: contain;
      }
    }
  }
}


@include breakpoint-down(extra-big) {
  .author {
    &__title {
      text-align: center;
      margin-bottom: 0;
    }
    &__inner {
      flex-direction: column;
      align-items: center;
    }

    &__list {
      gap: 2rem;
      justify-content: center;
      justify-items: center;
      margin-bottom: 3rem;
      width: 100%;
      max-width: 58rem;
    }

    &__item {
      max-width: fit-content;
    }

    &__img {
      display: flex;
      order: -1;
      max-width: 55rem;
      position: static;
      margin-inline: auto;
      margin-bottom: 4rem;
      height: 100%;

      img {
        max-height: 66rem;
      }

      &--temporarily {
        display: none;
      }
    }

    &__nums {
      max-width: 100%;
    }

    &__descr {
      max-width: 30rem;
    }
  }
}

@include breakpoint-down(big) {
  .author {
    &__img {
      right: 0;

      img {
        max-height: 66rem;
      }
    }

    &__list {
      margin-bottom: 2rem;
    }
  }
}

@include breakpoint-down(regular) {
  .author {
    &__descr,
    &__nums {
      max-width: 100%;
    }

    &__descr {
      font-size: 1.6rem;
    }
  }
}

@include breakpoint-down(small) {
  .author {
    &__list {
      gap: 2rem 1rem;
    }
    &__img {
      top: 60px;
      left: 0;
      max-width: 36rem;
    }
    &__item {
      max-width: 16.5rem;
    }
  }
}

@include breakpoint-down(xsmall) {
}
